.footer {
   background-color: #3E3E48;
   color: #fff;
}
.footer-content {
   display: flex;
   justify-content: space-between;
   padding: 80px 0;
   border-bottom: 1px solid #e5e5e5;

		&__left {
         width: 100%;
		}

		&__left-content {
		}

		&__item {
         font-family: 'Manrope-Regular', sans-serif;
         font-style: normal;
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         margin-bottom: 30px;
		}

      &__link {
      }

		&__right {
         display: flex;
         justify-content: space-between;
         width: 100%;
		}

		&__right-column {
         width: 100%;
		}
}
.footer-bottom {
   padding: 30px 0;
   display: flex;
   justify-content: space-between;
   &__logo {
      font-family: 'PlayfairDisplay-Regular', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 140%;
      letter-spacing: 0.15em;
      text-transform: uppercase;
   }
   &__phone {
      font-family: 'Manrope-Regular', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: calc(16px + 8 * (100vw / 1200));
      line-height: 140%;
   }
}
